*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none !important;
}
body {
  font-family: "Carlito", sans-serif;
  letter-spacing: 0.02em;
  font-size: 16px;
  line-height: 1.3em;
}
[hidden] {
  display: none !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Unbounded", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  line-height: 1.3em;
}
.material-symbols-outlined {
  font-variation-settings: 'FILL' 0, 'wght' 500, 'GRAD' 0, 'opsz' 24;
}
.center-title {
  text-align: center;
  display: block;
  width: 100%;
}
.link {
  color: #5493e4;
  text-decoration: none;
  cursor: pointer;
}
.link svg {
  fill: #5493e4;
}
.link:hover {
  color: #3573c4;
}
.link:hover svg {
  fill: #3573c4;
}
.link .arrow {
  height: 0.8em;
}
.text-link {
  color: #5493e4;
  text-decoration: none;
  cursor: pointer;
  border-bottom: 1px solid transparent;
}
.text-link svg {
  fill: #5493e4;
}
.text-link:hover {
  color: #3573c4;
}
.text-link:hover svg {
  fill: #3573c4;
}
.text-link .arrow {
  height: 0.8em;
}
.text-link:hover {
  border-bottom: 1px solid #a8ceff;
}
.main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.main-wrapper {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: #ddd;
  overflow: hidden;
}
.reglink {
  position: relative;
}
.currentreg {
  position: fixed;
  top: 10px;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  z-index: 1;
}
.reglabel {
  position: fixed;
  left: 50%;
  top: 0;
  padding: 5px 10px;
  background-color: #fff;
  border-radius: 1000px;
}
.reglink path:hover + .reglabel {
  display: block;
}
.maps {
  position: absolute;
  top: 0;
  left: 0;
  width: 1000px;
  transform: translate(-50%, -50%);
}
.maps-wrapper {
  position: relative;
}
.map {
  z-index: 2;
  position: absolute;
  opacity: 0.7;
  width: 87.3049%;
  top: -2.5%;
  left: 1.4%;
  stroke: #333;
}
.map path.current {
  fill: #ffcb1d !important;
}
.map path.empty {
  opacity: 0.3;
}
.map a path {
  fill: #fffb;
}
.map a path:hover {
  fill: #ffebc4ba;
}
.map a:visited path {
  fill: #fffb;
}
.map a:visited path:hover {
  fill: #ffebc4ba;
}
.undermap {
  opacity: 0;
  width: 100%;
}
.region-popup {
  position: relative;
  display: block;
  top: 60px;
  left: 20px;
  bottom: 20px;
  width: 320px;
  padding: 15px;
  background-color: #fff;
  border-radius: 10px;
}
.region-popup__top {
  display: flex;
  align-items: center;
}
.region-popup__title {
  font-weight: 900;
}
.region-popup__close {
  margin-left: auto;
  text-decoration: none;
  color: #000;
}
.people-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #0008;
  padding: 30px 0;
  overflow-y: auto;
  z-index: 2;
}
.people-popup__wrapper {
  margin: 0 auto;
  background-color: #fff;
  border-radius: 20px;
  width: 700px;
  padding: 30px;
  max-width: 95%;
}
.people-popup__top {
  display: flex;
  align-items: center;
  padding-bottom: 10px;
}
.people-popup__close {
  margin-left: auto;
  text-decoration: none;
  color: #000;
}
.people-popup p {
  margin: 12px 0;
}
.people-popup__params {
  margin: 10px 0;
}
.people-popup__param-name {
  width: 250px;
  margin-right: 5px;
  opacity: 0.5;
  font-style: italic;
}
.people-popup__param-name::after {
  content: ':';
}
.people-popup__images {
  margin-bottom: -10px;
}
.people-popup__images img {
  width: 100%;
  margin: 10px 0;
  border-radius: 10px;
}
.people-popup__youtube {
  margin-top: 25px;
}
